.gpt3__header {
  display: flex;
  flex-direction: row;
}

.gpt3__header-content {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  flex: 1;

  margin-right: 5rem;
}

.gpt3__header-content__title {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.gpt3__header-content__paragraph {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  color: var(--color-text);

  margin-top: 1.5rem;
}

.gpt3__header-content__block {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 2rem 0 1rem;
}

.gpt3__header-content__block-input {
  flex: 2;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  padding: 0 1rem;
  outline: none;
  color: #fff;

  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.gpt3__header-content__block-button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 28px;
  background: #FF4820;
  border: 2px solid #FF4820;
  padding: 0 1rem;
  color: #fff;
  cursor: pointer;
  outline: none;

  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;

  margin-top: 2rem;
}

.gpt3__header-content__people-image {
  width: 182px;
  height: 38px;
}

.gpt3__header-content__people-paragraph {
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #fff;
  text-align: center;
}

.gpt3__header-image__block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.gpt3__header-image__block-image {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1024px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content {
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header-content__title {
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people-paragraph {
    margin: 0;
  }

  .gpt3__header-content__block-input,
  .gpt3__header-content__block-button {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__header-content__title {
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header-content__paragraph {
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__block-input,
  .gpt3__header-content__block-button  {
    font-size: 12px;
    line-height: 16px;
  }
}